import React, { Component } from 'react';
import {TransitionablePortal, Segment, Label, Button, Header} from 'semantic-ui-react';
import color from '../colors';
// import CategoryItems from './CategoryItems'

class CategoryPortal extends Component {

    state = { activeIndex: -1, category: "" }

    handleClick = (e, titleProps) => {
        console.log(titleProps);
        const { index } = titleProps
        const { activeIndex } = this.state
        const newIndex = activeIndex === index ? -1 : index

        this.setState({ activeIndex: newIndex, category: titleProps.children })
    }

    // stopPropagation = (e) => {
    //     e.stopPropagation();
    //   }

    render() {
        const fruitItems = this.props.items.filter(item => (item.type === "Fruit"));
        const fruitCategories = [...new Set(fruitItems.map(item => item.tags))].sort();
        const renderedFruitCategories = fruitCategories.map((category,index) => {
            return (
                <span key={index}>
                    <Label onClick={this.props.categorySelected.bind(this,category)} style={{width:'50%', height:36, backgroundColor:'transparent', color:color(category)}}>{category}</Label>
                </span>
            )
        });
        const vegItems = this.props.items.filter(item => (item.type === "Veg"));
        const vegCategories = [...new Set(vegItems.map(item => item.tags))].sort();
        const renderedVegCategories = vegCategories.map((category,index) => {
            return (
                <span key={index}>
                    <Label
                        onClick={this.props.categorySelected.bind(this,category)}
                        style={{
                            width:'50%', 
                            height:32, 
                            backgroundColor:'transparent', 
                            color:color(category)
                        }}
                    >
                        {category}
                    </Label>
                </span>
            )
        });
        // const categories = [...new Set(this.props.items.map(item => item.tags))].sort();
        // const renderedCategories = categories.map((category,index) => {
        //     return (
        //         <span key={index}>
        //             <Label onClick={this.props.categorySelected.bind(this,category)} style={{width:'50%', height:36, margin:0, backgroundColor:'transparent', color:color(category)}}>{category}</Label>
        //         </span>
        //     )
        // });
        return (
            <div>
                <TransitionablePortal 
                    open={this.props.open} 
                    transition={{animation:'horizontal flip', duration:500}}  
                    closeOnDocumentClick={false}
                >
                    {/* <div style={{ backgroundColor: 'transparent', left: '0%', padding:0, position: 'fixed', top: '62px', zIndex: 5002, width:"100%", height:"100%", borderRadius:0}}
                        onClick={this.props.onClose}
                    > */}
                    <Segment inverted textAlign="center"
                        style={{
                            borderRadius:"0em",
                            position: 'fixed',
                            top: '60px',
                            right: '0%',
                            width:"50%",
                            height:"auto",
                            margin:0,
                            paddingTop:10,
                            paddingBottom:0,
                            zIndex: 5004
                        }}
                        // onClick={this.stopPropagation}
                    >
                        <Header disabled content="- FRUIT -" inverted size="tiny" onClick={this.props.fruitSelected.bind(this, 'Fruit',"*")}/>
                        {renderedFruitCategories}
                        <Header disabled content="- VEG -" inverted size="tiny" onClick={this.props.vegSelected.bind(this, 'Veg',"*")}/>
                        {renderedVegCategories}
                    </Segment>
                    {/* </div> */}
                </TransitionablePortal>
            </div>
        );
    }
}

export default CategoryPortal;



