import React, { Component } from 'react';
import {TransitionablePortal, Segment, Label, Button, Divider, Header, Icon} from 'semantic-ui-react';
import color from '../colors';
// import CategoryItems from './CategoryItems'

class SortPortal extends Component {

    state = { activeIndex: -1, category: "" }

    handleClick = (e, titleProps) => {
        console.log(titleProps);
        const { index } = titleProps
        const { activeIndex } = this.state
        const newIndex = activeIndex === index ? -1 : index

        this.setState({ activeIndex: newIndex, category: titleProps.children })
    }

    // stopPropagation = (e) => {
    //     e.stopPropagation();
    //   }

    render() {

        const sortButton = (sortMode, label = sortMode) => {
            return (
                <Button onClick={this.props.sortModeSelected.bind(this,sortMode)}>
                    {(this.props.sortMode === sortMode) ? <Icon name="check"/> : null}
                    {label}
                </Button>
            )
        }
        const categories = [...new Set(this.props.items.map(item => item.tags))].sort();
        const renderedCategories = categories.map((category,index) => {
            return (
                <span key={index}>
                    <Label onClick={this.props.categorySelected.bind(this,category)} style={{width:'50%', height:36, margin:0, backgroundColor:'transparent', color:color(category)}}>{category}</Label>
                </span>
            )
        });
        return (
            <div>
                <TransitionablePortal 
                    open={this.props.open} 
                    transition={{animation:'horizontal flip', duration:500}}  
                    closeOnDocumentClick={false}
                    onOpen={this.props.viewChangeHandler.bind(this, "Sort")}
                >
                    {/* <div style={{ backgroundColor: 'transparent', left: '0%', padding:0, position: 'fixed', top: '62px', zIndex: 5002, width:"100%", height:"100%", borderRadius:0}}
                        onClick={this.props.onClose}
                    > */}
                    <Segment inverted textAlign="center"
                        style={{ borderRadius:"0em", right: '0%', margin:0, paddingTop:0, paddingBottom:0, position: 'fixed', top: '54px', zIndex: 5004, width:"50%", height:"auto"}}
                        // onClick={this.stopPropagation}
                    >
                        <Button.Group size="massive" compact className="black">
                            <Button onClick={this.props.fruitSelected.bind(this, 'Fruit',"*")} icon="apple"/>
                            <Button onClick={this.props.vegSelected.bind(this, 'Veg',"*")} icon="leaf"/>
                        </Button.Group>
                        {/* <Divider/> */}
                        <p/>
                        <Header dividing>Sort by</Header>
                        <Button.Group vertical className="black">
                            {sortButton("Alphabetical")}
                            {sortButton("Grouped")}
                            {sortButton("Timestamp", "Completion Order")}
                        </Button.Group>
                        <p/>
                        <Button className="black" onClick={this.props.sortDirectionChanged.bind(this)}>[{this.props.reverse?"Descending":"Ascending"}]</Button>
                        {/* <Divider style={{marginTop:20, marginBottom:20}}/> */}
                        <Segment inverted>
                            <Button inverted basic icon="check" size="massive"
                                onClick={this.props.completedClicked}
                            />
                        </Segment>
                    </Segment>
                    {/* </div> */}
                </TransitionablePortal>
            </div>
        );
    }
}

export default SortPortal;



